                        <el-table-column prop="ev_3" label="类型" width="180">
                        </el-table-column>
                        <el-table-column prop="ev_4" label="释放者">
                            <template slot-scope="scope">
                                {{ scope.row.ev_4 | char_Name(name) }}
                            </template>
                        </el-table-column>
